import { useState, useEffect } from 'react';
import styles from'./Projects.module.css';
import { motion } from 'framer-motion'
import { useTheme } from '../hooks/ThemeCtx'

export default function Projects () {
  const [projects, setProjects] = useState()
  const {dark, setDark } = useTheme()

  useEffect (() => {
    const fetchprojects = async () => {
      const raw = await fetch("projects.json")
      const data = await raw.json()
      setProjects(data)
    }
    fetchprojects()
  }, [])

  return (
  <>
    <h1>Projects</h1>
    <main className={styles.grid}>
      {projects && projects.map(project => (
        <motion.a key={project.id}
          className={styles.card}
          whileHover={{scale:1.01}}
          href={project.link} 
          target="_blank" 
          style= {{
              background: dark ? "linear-gradient(30deg, #D2691E 0%, #2F4F4F 33%, #4682B4 93%)"
                               : "linear-gradient(30deg, #E0FFFF, #AFEEEE)",
              border: dark ? "1px solid #444" : "1px solid #AFEEEE",
              color: dark ? "#ddd" : "#444",
          }}
        >
          <div className={styles.image}
              style={{
                backgroundImage: `url(${project.image})`,
              }}
          >
          </div>
          <h2>{project.name}</h2>
          <div className={styles.tags}>
            {project.tech.map(t => (
                <span className={styles.tag} key={t}>{t}</span>
            ))}
          </div>
          <div className={styles.description}>
            <p>{project.description}</p>
          </div>
        </motion.a>
      ))}
    </main>
  </>
  )
}
