import './App.css';
import styles from "./App.module.css"
import Toggle from './comps/toggle'
import SmallScreenBar from './comps/smallScreenBar'
import Home from './pages/Home'
import Colors from './pages/Colors'
import Contact from './pages/Contact'
import {BrowserRouter as Router, Routes, Route, NavLink } from 'react-router-dom';

import { useTheme } from './hooks/ThemeCtx'
import { useIsOpenSmallMenu } from './hooks/SmallScreenMenuCtx'
import { useToggleSmallMenu } from './hooks/SmallScreenMenuCtx'

function App() {
  const {dark, setDark } = useTheme()
  const isOpen = useIsOpenSmallMenu()
  const setIsOpen = useToggleSmallMenu()

  let activeStyle = {
    color: "dodgerblue",
    fontWeight: "bold",
    textShadow: "0px 0px 3px white,0px 0px 5px white,0px 0px 8px white,0px 0px 11px white",
  };

  const colors = {
    background: dark ? "linear-gradient(30deg, #D2691E 0%, #2F4F4F 33%, #4682B4 93%)"
                     : "linear-gradient(30deg, #E0FFFF, #AFEEEE)",
    border: dark ? "1px solid #444" : "1px solid #AFEEEE",
    color: dark ? "#ddd" : "#444",
  }

  return (
      <Router>
      <div className={styles.container}>
        <div style={{
          display: !window.matchMedia(`(max-width: 500px)`).matches ? "none" : "flex",
          }}
        >
          <SmallScreenBar /> 
        </div>

      <nav className={styles.nav}
        style= {{
          display: (window.matchMedia(`(max-width:500px)`).matches && isOpen) ? "none" : "flex",
                ...colors,

        }}
      > 
        <ul>
            <li onClick={() => setIsOpen(false)}>
            <NavLink to="/" end
              style={({ isActive }) =>
                isActive ? activeStyle : undefined
              }
            >
              Home
            </NavLink>

              </li>
            <li onClick={() => setIsOpen(false)}>
            <NavLink to="/colors"
              style={({ isActive }) =>
                isActive ? activeStyle : undefined
              }
            >
              Colors
            </NavLink>

              </li>
          {/*<li>Projects</li>
          <li>Blog</li>*/}
            <li onClick={() => setIsOpen(false)}>
            <NavLink to="contact"
              style={({ isActive }) =>
                isActive ? activeStyle : undefined
              }
            >
              Contact
            </NavLink>

            </li>
        </ul>
        <div onClick={() => setIsOpen(!isOpen)}
          style={{
            display: !window.matchMedia(`(max-width: 500px)`).matches ? "none" : "flex",
          }}
        >
          Close
        </div>

        <div className={styles.controls}>
          <Toggle />
        </div>
      </nav>

      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="colors" element={<Colors />} />
        <Route path="contact" element={<Contact />} />
      </Routes>

      <footer className={styles.footer}
        style={{
          ...colors,
        }}
      > 2022 Copyright
      </footer>

    </div>
    </Router>
  );
}

export default App;
