import styles from "./SmallScreenBar.module.css"
import { useTheme } from '../hooks/ThemeCtx'
import { useIsOpenSmallMenu } from '../hooks/SmallScreenMenuCtx'
import { useToggleSmallMenu } from '../hooks/SmallScreenMenuCtx'

function SmallScreenBar() {
  const {dark, setDark } = useTheme()
  const isOpen = useIsOpenSmallMenu()
  const setIsOpen = useToggleSmallMenu()
  console.log("from SmallScreenBar:", " isOpen: ", isOpen)

  return (
    <div className={styles.container}>
      <nav className={styles.nav}
        style= {{
          background: dark ? "linear-gradient(30deg, #D2691E 0%, #2F4F4F 33%, #4682B4 93%)"
                           : "linear-gradient(30deg, #E0FFFF, #AFEEEE)",
          border: dark ? "1px solid #444" : "1px solid #AFEEEE",
          color: dark ? "#ddd" : "#444",
        }}
      > 
        <h1>Farukara</h1>
        <div className={styles.controls}
          onClick={() => setIsOpen(!isOpen)}
        >
          Menu
          <div className={styles.burger}>
            {[...Array(3)].map((x,i) => {
              return (
                <div key={i}
                  style={{
                    backgroundColor: dark ? "#ddd" : "#444",
                  }}
                >
                </div>
              )
            })
            }
          </div>
        </div>
      </nav>
    </div>
  );
}

export default SmallScreenBar;
